<template>
    <div id="commemorations">
        <v-card class="commemoration" v-for="item in items" :key="item.id">
            <v-card-title>第{{item.days}}天</v-card-title>
            <v-card-subtitle>{{item.title}}</v-card-subtitle>
        </v-card>
    </div>
</template>

<script>
    import axios from 'axios';
    import util from '../util/util'

    export default {
        name: "commemoration",
        data() {
            return {
                items: []
            }
        },
        mounted() {
            this.getCommemorations()
        },
        methods: {
            getCommemorations() {
                let vm = this;
                axios.post('https://www.10000hours.love:520/commemoration/queryAll')
                    .then(function (response) {
                        let result = response.data;
                        if (result.error === 1) {
                            let list = JSON.parse(result.body);
                            let milliseconds = new Date().getTime();
                            for (let i = 0; i < list.length; i++) {
                                let days = util.diffTime(list[i].startTimeMills, milliseconds).days;
                                if (milliseconds - list[i].startTimeMills > 0) {
                                    list[i].days = days + 1
                                } else {
                                    list[i].days = -(days - 1)
                                }
                            }
                            vm.items = list
                        }
                    }).catch(function (error) {
                    console.log(error)
                });
            }
        }
    }
</script>

<style scoped>
    .commemoration {
        margin: 10px;
    }
</style>